<template>
  <v-bottom-sheet v-model="sheet">
    <template v-slot:activator="{ on }">
      <v-text-field
        v-model="text"
        :label="label"
        :rules="rules"
        prepend-icon="event"
        readonly
        v-on="on"
      />
    </template>
    <v-sheet class="text-right pt-2">
      <v-date-picker
        :value="dates"
        v-bind="$attrs"
        no-title
        range
        full-width
        @input="change"
      />
    </v-sheet>
  </v-bottom-sheet>
</template>

<script>
export default {
  name: 'FormatDateRange',

  inheritAttrs: false,

  props: {
    value: {
      type: [String, Array],
      required: true,
    },
    separator: String,
    label: String,
    rules: Array,
  },

  computed: {
    text() {
      return this.dates.join(this.separator || '~')
    },
  },

  watch: {
    value: {
      handler(value) {
        this.dates = Array.isArray(value)
          ? [...value]
          : this.separator && value
          ? this.value.split(this.separator)
          : []
      },
      immediate: true,
      deep: true,
    },
  },

  data() {
    return {
      sheet: false,
      dates: [],
    }
  },

  methods: {
    change(value) {
      this.dates = value
      if (value.length === 2) {
        const val = this.separator ? value.join(this.separator) : value
        this.$emit('input', val)
        this.sheet = false
      }
    },
  },
}
</script>

<style lang="scss" scoped></style>
