<template>
  <v-bottom-sheet v-model="sheet">
    <template v-slot:activator="{ on }">
      <slot :on="on">
        <span>
          <img v-show="value" :src="value" alt="" height="20" v-on="on" />
          <span v-show="!value" v-on="on" class="grey--text text--lighten-2"
            >点击签名</span
          >
        </span>
      </slot>
    </template>
    <v-sheet class="">
      <v-card lighten>
        <v-responsive :aspect-ratio="16 / 10">
          <v-card-text class="hp-10">
            <v-row class="hp-10 mb-n9 pb-9">
              <VueSignaturePad
                class="signature-pad elevation-1"
                ref="signaturePad"
              />
            </v-row>
            <v-row>
              <v-btn text icon @click="undo"><v-icon>mdi-undo</v-icon></v-btn>
              <v-btn text icon @click="clear"
                ><v-icon>mdi-eraser</v-icon></v-btn
              >
            </v-row>
          </v-card-text>
        </v-responsive>
        <v-card-actions>
          <v-row>
            <v-btn text @click="close">取消</v-btn>
            <v-spacer />
            <v-btn text color="primary" @click="save">确定</v-btn>
          </v-row>
        </v-card-actions>
      </v-card>
    </v-sheet>
  </v-bottom-sheet>
</template>

<script>
export default {
  name: 'SignatureDialog',

  props: {
    value: String,
  },

  watch: {
    sheet(value) {
      if (value) {
        this.value &&
          this.$nextTick(() => {
            this.$refs.signaturePad.fromDataURL(this.value)
          })
      }
    },
  },

  data() {
    return {
      sheet: false,
    }
  },

  methods: {
    open(data) {
      this.sheet = true
      this.$nextTick(() => {
        data && this.$refs.signaturePad.fromDataURL(data)
      })
    },
    save() {
      const { isEmpty, data } = this.$refs.signaturePad.saveSignature()
      if (!isEmpty) this.$emit('save', data)
      this.close()
    },
    close() {
      this.clear()
      this.sheet = false
    },
    undo() {
      this.$refs.signaturePad.undoSignature()
    },
    clear() {
      this.$refs.signaturePad.clearSignature()
    },
  },
}
</script>

<style lang="scss" scoped></style>
