<template>
  <v-bottom-sheet v-model="sheet">
    <template v-slot:activator="{ on }">
      <slot name="activator" :on="on">
        <v-text-field
          :value="$attrs.value"
          :label="label"
          :rules="rules"
          prepend-icon="event"
          readonly
          v-on="on"
        />
      </slot>
    </template>
    <v-sheet class="">
      <slot :close="close" />
      <v-date-picker
        v-bind="$attrs"
        v-on="$listeners"
        full-width
        ref="picker"
        @change="changeHandler"
      />
    </v-sheet>
  </v-bottom-sheet>
</template>

<script>
export default {
  name: 'DatePickerSheet',

  props: {
    label: String,
    rules: Array,
    stopChange: Boolean,
    initView: {
      type: String,
      default: 'year',
    },
  },

  watch: {
    sheet(value) {
      value &&
        this.$nextTick(() => {
          this.$refs.picker.activePicker = this.initView.toUpperCase()
        })
    },
  },

  data() {
    return {
      sheet: false,
    }
  },

  methods: {
    close() {
      this.sheet = false
    },
    changeHandler() {
      if (this.stopChange) return
      this.close()
    },
  },
}
</script>

<style lang="scss" scoped></style>
