import {
  VTextField as text,
  VSelect as select,
  VRadioGroup as radio,
} from 'vuetify/lib'
const tagTextMap = {
  text: '请输入',
  select: '请选择',
  radio: '请选择',
  'format-date-range': '请选择',
  month: '请选择',
}
const components = {
  text,
  select,
  radio,
  month: 'birthday-picker',
}
export default {
  functional: true,

  name: 'VFormItem',

  props: {
    label: String,
    type: {
      type: String,
      required: true,
    },
    required: Boolean,
    rules: {
      type: Array,
      default: () => [],
    },
    typeAttr: String,
    message: String,
  },

  render(h, context) {
    const { type, label, required, rules, typeAttr, message } = context.props
    const comp = components[type] || type
    const requiredRule = required
      ? [
          v =>
            !!v ||
            message ||
            `${tagTextMap[type] || ''}${label || 'is required!'}`,
        ]
      : []
    const props = {
      ...context.data.attrs,
      label,
      rules: rules.concat(requiredRule),
    }
    if (typeAttr) {
      props.type = typeAttr
    }
    return h(comp, { props, ...context.data }, context.children)
  },
}
