<template>
  <v-menu
    ref="menu"
    v-model="menu"
    :close-on-content-click="false"
    transition="scale-transition"
    offset-y
    min-width="290px"
  >
    <template v-slot:activator="{ on }">
      <v-text-field
        v-model="date"
        :label="label"
        prepend-icon="event"
        readonly
        v-on="on"
      ></v-text-field>
    </template>
    <v-date-picker
      ref="picker"
      v-model="date"
      v-bind="$attrs"
      :max="new Date().toISOString().substr(0, 10)"
      min="1950-01-01"
      @change="save"
    ></v-date-picker>
  </v-menu>
</template>

<script>
export default {
  name: 'BirthdayPicker',

  props: {
    value: String,
    label: {
      type: String,
      default: '出生日期',
    },
  },

  watch: {
    value: {
      handler(value) {
        this.date = value
      },
      immediate: true,
    },
    menu(val) {
      val && setTimeout(() => (this.$refs.picker.activePicker = 'YEAR'))
    },
  },

  data() {
    return {
      menu: false,
      date: null,
    }
  },

  methods: {
    save(date) {
      this.$refs.menu.save(date)
      this.$emit('input', date)
    },
  },
}
</script>

<style lang="scss" scoped></style>
